import {
	mapState
} from 'vuex'
import driver from './driver/driver.vue'
export default {
	components: {
		driver
	},
	computed: mapState(['hasLogin', 'userName', 'userInfo']),
	onLoad() {
		if (!this.hasLogin) {
			uni.showModal({
				title: '未登录',
				content: '您未登录，需要登录后才能继续',
				success: (res) => {
					if (res.confirm) {
						uni.navigateTo({
							url: '../login/login'
						});
					}
				}
			});
		}
	}
}
